export default class Difference {

    constructor(officer, counter, items) {

        try {

            this.officer = document.querySelector(officer);

            this.items = this.officer.querySelectorAll(items);
    
            this.counter = counter;

        } catch(e){}
    }

    bindTriggers() {

        this.officer.querySelector('.plus').addEventListener('click', () => {

            if (this.counter !== this.items.length - 2) {

                this.items[this.counter].style.display = 'flex';
                this.items[this.counter].classList.add('animated');
                this.items[this.counter].classList.add('slideInDown');                   
                this.counter++;
            
            } else {

                this.items[this.counter].style.display = 'flex';
                this.items[this.items.length - 1].classList.add('animated');
                this.items[this.items.length - 1].classList.add('slideOutDown');                   
                // this.items[this.items.length - 1].remove();
            }         
        });
   
    }

    hideItems() {
        this.items.forEach((item, i, arr) => {

            if (i !== arr.length - 1) {
                item.style.display = 'none';
            }
        });
   
    }

    init() {

        try {

            this.hideItems();

            this.bindTriggers();

        } catch(e){}
    }
}